import BaseApi from "./base-api"

class Template extends BaseApi {
  createTemplate = params => this.client.post("/templates", params)
  getTemplates = page => this.client.get("/templates?page=" + page)
  getTemplate = id => this.client.get("/templates/" + id)
  deleteTemplate = id => this.client.delete("/templates/" + id)
}

export const TemplateApi = new Template()
