import BaseApi from "./base-api"

class Scan extends BaseApi {
  createScan = params => this.client.post("/scans", params)
  getScans = page => this.client.get("/scans?page=" + page)
  deleteScan = task_code => this.client.delete("/scans/" + task_code)
  downloadResult = task_code => this.client.get("scans/" + task_code + "/download")
}

export const ScanApi = new Scan()
