import React, { Suspense } from "react"
import { Provider } from "react-redux"
import { Switch, Router } from "react-router-dom"
import { PersistGate } from "redux-persist/integration/react"
import { ThemeProvider } from "styled-components"
import history from "configs/history"
import defaultTheme from "assests/theme"

import PrivateRoute from "components/routes/private-route"
import PublicRoute from "components/routes/public-route"
import SwichRoute from "components/routes/switch-route"

import { store, persistor } from "configs/store"

import MainLayout from "components/layouts/main-layout"
import Login from "screens/auth/auth"
import Dashboard from "screens/dashboard"
import Scan from "screens/scan"

import "screens/App.css";

function App() {
  return (
    <Suspense fallback={false}>
      <Provider store={store}>
        <PersistGate persistor={persistor}>
          <ThemeProvider theme={defaultTheme}>
            <Router history={history}>
              <Switch>
                <PublicRoute exect path="/login" component={Login} />
                <PrivateRoute exect path="/dashboard" layout={MainLayout} component={Dashboard} />
                <PrivateRoute exect path="/scan/:id" layout={MainLayout} component={Scan} />
                <SwichRoute exect path="/" />
              </Switch>
            </Router>
          </ThemeProvider>
        </PersistGate>
      </Provider>
    </Suspense>
  )
}

export default App;
