import { takeLatest, all, call } from "redux-saga/effects"
import { ScanApi } from "services/api/scan-api"
import { notification as toast } from "antd"
import history from "configs/history"

import { createScan } from "./scan.action"

function* createScanSaga(action) {
  yield call([ScanApi, ScanApi.createScan], action.payload)

  toast.success({
    message: "Success",
    description: "Task is processing"
  });
  history.push("/dashboard");
}

export default function*() {
  yield all([
    takeLatest(createScan, createScanSaga)
  ])
}
