import React from "react"
import { connect } from "react-redux"
import { Form, Input, Button } from "antd"
import { withFormik } from "formik"
import { compose } from "recompose"

import { authValidation } from "models/auth"
import { loginData } from "models/form-data"

import { statusError, tError } from "ultiz/form-ultiz"
import { withTranslation } from "react-i18next"

import { login } from "./auth.actions"

import styles from "./style.module.scss"

type Props = {
  values: object,
  errors: object,
  t: object,
  handleChange: Function,
  handleSubmit: Function,
  handleBlur:Function,
  touched: Function
}

const Auth = (props: Props) => {
  const layout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 20 },
  }
  const tailLayout = {
    wrapperCol: { offset: 4, span: 20 },
  }
  const { values, errors, t, handleChange, handleSubmit, handleBlur, touched } = props

  return (
    <div className={styles.box_center}>
      <Form
        {...layout}
        initialValues={{ remember: true }}
        onFinish={() => { handleSubmit() }}
      >
        <h3 className={styles.text_title}>GUI DEEPSCAN</h3>
        <Form.Item
          label="Email"
          name="email"
          validateStatus={statusError(errors.email, "email", touched)}
          help={errors.email && tError(errors.email, t)}
          value={values.email}
        >
          <Input
            onChange={handleChange}
            onBlur={handleBlur} />
        </Form.Item>

        <Form.Item
          label="Password"
          name="password"
          validateStatus={statusError(errors.password, "password", touched)}
          help={errors.password && tError(errors.password, t)}
          value={values.password}
        >
          <Input.Password
            onChange={handleChange}
            onBlur={handleBlur} />
        </Form.Item>

        <Form.Item {...tailLayout}>
          <Button type="primary" htmlType="submit">
            Login
          </Button>
        </Form.Item>
      </Form>
    </div>
  )
}

export default compose(
  withTranslation("login"),
  connect(null, { login }),
  withFormik({
    mapPropsToValues: () => (loginData),
    validationSchema: authValidation,
    handleSubmit: (values, { props, setSubmitting, setErrors }) => {
      props.login({ values, meta: { t: props.t, setSubmitting, setErrors }});
    }
  })
)(Auth);
