import { handleActions } from "redux-actions";

import { updatePaginateTemplates,
  updatePaginateScans,
  duplicateTemplate,
  createTemplate,
  loadingTemplate,
  deleteTemplate,
  deleteScan,
  loadingScan,
  downloadResult } from "./dashboard.actions";

export const initialState = {
  paginateTemplate: {},
  paginateScan: {},
  isLoadingTemplate: false,
  isLoadingScan: false
};

export default handleActions({
  [updatePaginateTemplates]: (state, action) => ({ ...state, ...action.payload }),
  [updatePaginateScans]: (state, action) => ({ ...state, ...action.payload }),
  [duplicateTemplate]: (state, action) => ({ ...state, ...action.payload }),
  [createTemplate]: (state, action) => ({ ...state, ...action.payload }),
  [loadingTemplate]: (state, action) => ({ ...state, ...action.payload }),
  [deleteTemplate]: (state, action) => ({ ...state, ...action.payload }),
  [loadingScan]: (state, action) => ({ ...state, ...action.payload }),
  [deleteScan]: (state, action) => ({ ...state, ...action.payload }),
  [downloadResult]: (state, action) => ({ ...state, ...action.payload })
}, initialState);
