import { handleActions } from "redux-actions";

import { login, stateAuthLogin, logout } from "./auth.actions";

export const initialState = {
  isAuth: false
};

export default handleActions({
  [login]: (state, action) => ({ ...state, ...action.payload }),
  [stateAuthLogin]: (state, action) => ({ ...state, ...action.payload }),
  [logout]: (state, action) => ({ ...state, ...action.payload, isAuth: false })},
  initialState
);
