import { createActions } from "redux-actions"

const types = {
  LOGIN: "LOGIN",
  STATE_AUTH_LOGIN: "STATE_AUTH_LOGIN",
  LOGOUT: "LOGOUT"
}

export const {
  login,
  stateAuthLogin,
  logout
} = createActions({
  [types.LOGIN]: payload => payload,
  [types.STATE_AUTH_LOGIN]: payload => payload,
  [types.LOGOUT]: payload => payload
})
