import React from "react"
import { Redirect, Route } from "react-router-dom"

import { connect } from "react-redux"

import { authSelector } from "models/selector"
import NotFound from "screens/commons/404-not-found"

const SwitchRoute = ({component: Component, isAuth, ...rest }) => {
  if(rest.location.pathname === "/") {
    if(isAuth) return <Redirect to="/dashboard" />

    return <Redirect to="/login" />
  }

  return <Route component={NotFound} />
}

export default connect(authSelector, null)(SwitchRoute)
