import React, { useState } from "react"
import { Layout, Button } from "antd"
import { LoadingOutlined, UploadOutlined, FileProtectOutlined } from "@ant-design/icons"

import { getBase64 } from "ultiz/ultiz"

import { createScan } from "./scan.action"

import Header from "components/layouts/header"
import Footer from "components/layouts/footer"

import styles from "./scan.module.scss"
import { connect } from "react-redux"

const Scan = ({params, createScan}) => {
  const [loading, setLoading] = useState(false)
  const [imageName, setImageName] = useState(null)
  const [imageFile, setImageFile] = useState(null)

  const imageUpload = e => {
    setLoading(true);
    const file = e.target.files[0];
    getBase64(file).then(base64 => {
      setLoading(false);
      setImageName(file.name);
      setImageFile(file);
    });
  }

  const startScan = () => {
    const formData = new FormData();
    formData.append("template_id", params.id);
    formData.append("task_name", imageName);
    formData.append("source_file", imageFile, imageName);
    createScan(formData)
  }

  const iconUpload = () => {
    if (loading) return <LoadingOutlined className={styles.icon_upload_file} />
    if (imageName) return <FileProtectOutlined className={styles.icon_upload_file} />
    return <UploadOutlined className={styles.icon_upload_file} />
  }

  const textUpload = () => {
    if (imageFile) return imageName
    return "Drag & Drop Your File(s) or"
  }

  const btn = () => {
    if (imageFile)
      return <Button type="primary" onClick={() => { startScan() }} className={styles.btn_scan} size="large">Start Scan</Button>
    return (
      <div className={styles.upload_btn_wrapper}>
        <Button type="primary" className={styles.btn_upload} size="large">Upload file(s)</Button>
        <input
          className={styles.file_upload}
          type="file"
          id="imageFile"
          name="imageFile"
          onChange={ imageUpload }
        />
      </div>
    )
  }

  return (
    <Layout>
      <Header />
      <Layout>
        <Layout className={styles.site_layout_background}>
          { iconUpload() }
          <p className={styles.mb30}>{ textUpload() }</p>
          { btn() }
        </Layout>
      </Layout>
      <Footer />
    </Layout>
  );
}

export default connect(null, {createScan})(Scan)
