import { createActions } from "redux-actions"

const types = {
  GET_TEMPLATES: "GET_TEMPLATES",
  UPDATE_PAGINATE_TEMPLATES: "UPDATE_PAGINATE_TEMPLATES",
  CREATE_TEMPLATE: "CREATE_TEMPLATE",
  DUPLICATE_TEMPLATE: "DUPLICATE_TEMPLATE",
  DELETE_TEMPLATE: "DELETE_TEMPLATE",
  LOADING_TEMPLATE: "LOADING_TEMPLATE",
  GET_SCANS: "GET_SCANS",
  UPDATE_PAGINATE_SCANS: "UPDATE_PAGINATE_SCANS",
  DELETE_SCAN: "DELETE_SCAN",
  LOADING_SCAN: "LOADING_SCAN",
  DOWNLOAD_RESULT: "DOWNLOAD_RESULT"
}

export const {
  getTemplates,
  updatePaginateTemplates,
  duplicateTemplate,
  createTemplate,
  deleteTemplate,
  getScans,
  updatePaginateScans,
  deleteScan,
  loadingTemplate,
  loadingScan,
  downloadResult
} = createActions({
  [types.GET_TEMPLATES]: payload => payload,
  [types.UPDATE_PAGINATE_TEMPLATES]: payload => payload,
  [types.DUPLICATE_TEMPLATE]: payload => payload,
  [types.CREATE_TEMPLATE]: payload => payload,
  [types.LOADING_TEMPLATE]: payload => payload,
  [types.DELETE_TEMPLATE]: payload => payload,
  [types.GET_SCANS]: payload => payload,
  [types.UPDATE_PAGINATE_SCANS]: payload => payload,
  [types.DELETE_SCAN]: payload => payload,
  [types.LOADING_SCAN]: payload => payload,
  [types.DOWNLOAD_RESULT]: payload => payload
})
