import { takeLatest, all, put, call } from "redux-saga/effects"
import history from "configs/history"
import { AuthApi } from "services/api/auth-api"
import { formikSaga } from "ultiz/form-ultiz"
import { notification as toast } from "antd"

import { login, stateAuthLogin, logout } from "./auth.actions"

function* loginSaga(action) {
  yield formikSaga(action, function* (values) {
    const response = yield call([AuthApi, AuthApi.signin], values)
    if(response) {
      yield put(stateAuthLogin({ isAuth: true }))
      yield localStorage.setItem("user_token", JSON.stringify(response.data.user_token))
      yield history.push("/dashboard")
    }
  })
}

function* logoutSaga() {
  yield call([AuthApi, AuthApi.signout], localStorage.getItem("user_token"))
  yield put(stateAuthLogin({ isAuth: false }))
  yield localStorage.removeItem("user_token")
  yield toast.success({
    message: "Success",
    description: "Logout"
  });

  yield history.push("/login")
}

export default function* () {
  yield all([
    takeLatest(login, loginSaga),
    takeLatest(logout, logoutSaga)
  ])
}
