import React, { useState, useEffect } from "react"
import { Layout, Table, Pagination, Button, Popconfirm, Row, Modal, message } from "antd"
import { Link } from "react-router-dom"
import { connect } from "react-redux"
import { compose } from "recompose"
import {
  UploadOutlined,
  FileProtectOutlined
} from "@ant-design/icons"

import {
  getTemplates,
  getScans,
  duplicateTemplate,
  loadingTemplate,
  deleteTemplate,
  loadingScan,
  deleteScan,
  createTemplate,
  downloadResult
} from "./dashboard.actions"

import { templatesSelector } from "./templates-selector"

import Header from "components/layouts/header"
import Footer from "components/layouts/footer"

import styles from "./dashboard.module.scss"

const Dashboard = ({ paginateTemplate,
  templates,
  getTemplates,
  loadingTemplate,
  paginateScan,
  scans,
  getScans,
  duplicateTemplate,
  deleteTemplate,
  isLoadingTemplate,
  deleteScan,
  isLoadingScan,
  createTemplate,
  loadingScan,
  downloadResult }) => {

  const [visible, setVisible] = useState(false)
  const [uploaded, setUploaded] = useState(false)
  const [templateParam, setTemplateParam] = useState("")

  const columnsTemplate = [
    {
      title: "Name",
      dataIndex: "template_name",
      key: "name",
      sorter: {
        multiple: 1,
        filterMultiple: true
      },
    },
    {
      title: "Created On",
      dataIndex: "created_at",
      key: "created_at",
      sorter: {
        multiple: 2,
        filterMultiple: true
      },
    },
    {
      title: "Owner",
      dataIndex: "owner",
      key: "owner",
      sorter: {
        multiple: 3,
        filterMultiple: true
      },
    },
    {
      title: "Action",
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => (
        <div>
          <Link className={styles.btn} to={"/scan/" + record.id} >Start Scan</Link>
          <Popconfirm title={"Are you duplicate template?"} onConfirm={() => duplicateTemplate(record.id)} >
            <Button className={styles.btn} >Duplicate</Button>
          </Popconfirm>
          <Popconfirm title={"Are you sure to delete this template?"} onConfirm={() => deleteTemplate(record.id)} >
            <Button className={styles.btn} >Delete</Button>
          </Popconfirm>
        </div>
      ),
      width: 400
    }
  ];

  const columnsScan = [
    {
      title: "Name",
      dataIndex: "task_name",
      key: "name",
      sorter: {
        multiple: 1,
        filterMultiple: true
      },
    },
    {
      title: "Task code",
      dataIndex: "task_code",
      key: "task_code",
      sorter: {
        multiple: 2,
        filterMultiple: true
      },
    },
    {
      title: "Scanned On",
      dataIndex: "created_at",
      key: "scanned_on",
      sorter: {
        multiple: 3,
        filterMultiple: true
      }
    },
    {
      title: "Owner",
      dataIndex: "user",
      key: "owner",
      sorter: {
        multiple: 4,
        filterMultiple: true
      }
    },
    {
      title: "Status",
      dataIndex: "status",
      key: "status",
      sorter: {
        multiple: 5,
        filterMultiple: true
      }
    },
    {
      title: "Action",
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => (
        <div>
          {record.status === "done"
            ? <Popconfirm title={"Are you sure to download the results?"} onConfirm={() => downloadResult(record.task_code)} >
              <Button className={styles.btn} >Download</Button>
            </Popconfirm>
            : <Button disabled className={styles.btn} >Download</Button>
          }
          <Popconfirm title={"Are you sure to delete scan?"} onConfirm={() => deleteScan(record.task_code)} >
            <Button className={styles.btn} >Delete</Button>
          </Popconfirm>
        </div>
      ),
      width: 350
    }
  ];

  const showPaginateScans = () => {
    if (paginateScan.current_page)
      return <Pagination
        pageSize={paginateScan.per_page}
        total={paginateScan.total_count}
        onChange={(page) => { getScans(page) }}
      />
  }

  const showPaginateTemplates = () => {
    if (paginateTemplate.current_page)
      return <Pagination
        pageSize={paginateTemplate.per_page}
        total={paginateTemplate.total_count}
        onChange={(page) => { getTemplates(page) }}
      />
  }

  useEffect(() => {
    getTemplates(1);
    getScans(1);
  }, [getScans, getTemplates])


  useEffect(() => {
    const interval = setInterval(() => {
      getTemplates(paginateTemplate.current_page ? paginateTemplate.current_page : 1)
      getScans(paginateScan.current_page ? paginateScan.current_page : 1)
    }, 10000);
    return () => clearInterval(interval);
  }, [getScans, getTemplates, isLoadingScan, isLoadingTemplate, loadingScan, loadingTemplate, paginateScan.current_page, paginateTemplate.current_page])

  useEffect(() => {
    if (isLoadingTemplate) {
      getTemplates(paginateTemplate.current_page ? paginateTemplate.current_page : 1)
      loadingTemplate({ isLoadingTemplate: false, isLoadingScan: false })
    }
    if (isLoadingScan) {
      getScans(paginateScan.current_page ? paginateScan.current_page : 1)
      loadingScan({ isLoadingScan: false, isLoadingTemplate: false })
    }
  }, [getScans, getTemplates, isLoadingScan, isLoadingTemplate, loadingScan, loadingTemplate, paginateScan.current_page, paginateTemplate.current_page])

  const iconUpload = () => {
    if (uploaded) return <FileProtectOutlined className={styles.icon_upload_file} />
    return <UploadOutlined className={styles.icon_upload_file} />
  }

  const onReaderLoad = e => {
    try {
      setTemplateParam({ template: JSON.parse(e.target.result) })
      setUploaded(true)
      message.success("upload file success")
    } catch (err) {
      message.error("upload file fail")
    }
  }

  const imageUpload = e => {
    const reader = new FileReader();
    reader.onload = onReaderLoad;
    reader.readAsText(e.target.files[0]);
  }

  const createTemp = () => {
    createTemplate(templateParam);
    setVisible(false);
    setUploaded("");
  }

  const btn = () => {
    if (uploaded)
      return <Button
        type="primary"
        onClick={() => createTemp()}
        className={styles.btn_create_template}
        size="large"
      >
        Create template
      </Button>
    return (
      <div className={styles.upload_btn_wrapper}>
        <Button type="primary" className={styles.btn_upload} size="large">Upload file(s)</Button>
        <input
          className={styles.file_upload}
          type="file"
          id="imageFile"
          name="imageFile"
          onChange={imageUpload}
        />
      </div>
    )
  }

  const sortScan = (pagination, filters, sorter) => {
    console.log(sorter);
  }

  const sortTemplate = (pagination, filters, sorter) => {
    console.log(sorter);
  }

  return (
    <Layout>
      <Header />
      <Layout className={styles.dashboard}>
        <Layout className={styles.site_layout_background}>
          <Table
            columns={columnsScan}
            dataSource={scans}
            pagination={false}
            rowKey="task_code"
            title={() => (<h3>Recent Scans</h3>)}
            footer={() => showPaginateScans()}
            onChange={sortScan}
            bordered />
        </Layout>

        <Layout className={styles.site_layout_background}>
          <Table
            columns={columnsTemplate}
            dataSource={templates}
            pagination={false}
            rowKey="id"
            title={() => (
              <Row justify="space-between">
                <h3>Templates</h3>
                <Button
                  icon={<UploadOutlined />}
                  type="primary"
                  onClick={() => setVisible(true)}
                >
                  Upload New Template
                </Button>
              </Row>
            )}
            footer={() => showPaginateTemplates()}
            onChange={sortTemplate}
            bordered />
        </Layout>
      </Layout>
      <Footer />
      <Modal
        title="Create template"
        visible={visible}
        width={"sm"}
        className={styles.box_modal}
        onOk={() => setVisible(false)}
        onCancel={() => setVisible(false)}
      >
        <Layout className={styles.layout_modal}>
          {iconUpload()}
          {btn()}
        </Layout>
      </Modal>
    </Layout>
  )
}

export default compose(
  connect(templatesSelector, {
    getTemplates,
    getScans,
    duplicateTemplate,
    deleteTemplate,
    loadingTemplate,
    deleteScan,
    loadingScan,
    createTemplate,
    downloadResult
  })
)(Dashboard)
