import axios, { AxiosRequestConfig } from "axios"

const DEFAULT_API_CONFIG: AxiosRequestConfig = {
  baseURL: process.env.REACT_APP_BASE_URL,
  timeout: 30000,
  apiVersion: "v1",
  headers: {"Access-Token": "", "Accept": "*/*", "Content-Type": "application/json"}
}

export default class API {
  constructor(config) {
    this.client = axios.create({
      ...DEFAULT_API_CONFIG,
      config
    })

    this.client.interceptors.request.use(config => {
      config.headers["Access-Token"] = localStorage.getItem("user_token") && JSON.parse(localStorage.getItem("user_token"))
      return config
    })

    this.client.interceptors.response.use(res => {
      return res
    }, function(err) {
      throw err.response.dataz
    })
  }
}
