import { message } from "antd"
import { takeLatest, all, put, call } from "redux-saga/effects"
import { TemplateApi } from "services/api/template-api"
import { ScanApi } from "services/api/scan-api"

import { CONFIG_PAGINATION } from "contants/pagination"

import { getTemplates,
  updatePaginateTemplates,
  getScans,
  duplicateTemplate,
  createTemplate,
  loadingTemplate,
  deleteTemplate,
  loadingScan,
  deleteScan,
  downloadResult } from "./dashboard.actions"

function* getTemplatesSaga(action) {
  const res = yield call([TemplateApi, TemplateApi.getTemplates], action.payload)
  const pages = res.data.pages ? res.data.pages : CONFIG_PAGINATION
  yield put(updatePaginateTemplates({ paginateTemplate: pages, templates: res.data.templates}))
}

function* duplicateTemplateSaga(action) {
  const res = yield call([TemplateApi, TemplateApi.getTemplate], action.payload)
  yield put(createTemplate(res.data))
}

function* createTemplateSaga(action) {
  try {
    yield call([TemplateApi, TemplateApi.createTemplate], action.payload.template)
    message.success("Create template success!")
    yield put(loadingTemplate({ isLoadingTemplate: true }))
  } catch {
    message.error("Create template fail!")
  }
}

function* deleteTemplateSaga(action) {
  try {
    yield call([TemplateApi, TemplateApi.deleteTemplate], action.payload)
    message.success("Delete template success!")
    yield put(loadingTemplate({ isLoadingTemplate: true }))
  } catch {
    message.error("Delete template fail!")
  }
}

function* deleteScanSaga(action) {
  try {
    yield call([ScanApi, ScanApi.deleteScan], action.payload)
    message.success("Delete scan success!")
    yield put(loadingScan({ isLoadingScan: true }))
  } catch {
    message.error("Delete scan fail!")
  }
}

function* getScansSaga(action) {
  const res = yield call([ScanApi, ScanApi.getScans], action.payload)
  const pages = res.data.pages ? res.data.pages : CONFIG_PAGINATION
  yield put(updatePaginateTemplates({ paginateScan: pages, scans: res.data.tasks}))
}

function* downloadResultSaga(action) {
  try {
    yield call([ScanApi, ScanApi.downloadResult], action.payload)
    message.success("Download scan success!")
  } catch {
    message.error("Download template fail!")
  }
}

export default function*() {
  yield all([
    takeLatest(getTemplates, getTemplatesSaga),
    takeLatest(duplicateTemplate, duplicateTemplateSaga),
    takeLatest(getScans, getScansSaga),
    takeLatest(createTemplate, createTemplateSaga),
    takeLatest(deleteTemplate, deleteTemplateSaga),
    takeLatest(deleteScan, deleteScanSaga),
    takeLatest(downloadResult, downloadResultSaga)
  ])
}
