import React from "react"
import { connect } from "react-redux"
import { Layout, Button, Menu, Row, Col } from "antd"
import { LogoutOutlined } from "@ant-design/icons"
import { Link } from "react-router-dom"

import { logout as logoutAction } from "screens/auth/auth.actions"

const Header = ({ logout }) => {
  return (
    <Layout.Header>
      <div className="logo" />
      <Row>
        <Col flex="auto">
          <Menu theme="dark" mode="horizontal" defaultSelectedKeys={["1"]}>
            <Menu.Item key="1"><Link to="/dashboard">Dashboard</Link></Menu.Item>
          </Menu>
        </Col>
        <Col flex="100px">
          <Button icon={<LogoutOutlined />} type="primary" danger onClick={() => logout({ isAuth: false })}>
            Logout
          </Button>
        </Col>
      </Row>
    </Layout.Header>
  )
}

export default connect(null, { logout: logoutAction })(Header)
