import React from "react"
import { Redirect } from "react-router-dom"
import { connect } from "react-redux"

import { authSelector } from "models/selector"

const UnAuthRouter = ({ component: Component, isAuth, ...rest }) => {
  if (isAuth) return <Redirect to="/dashboard" />

  return <Component />
}

export default connect(authSelector, null)(UnAuthRouter)
